

import { formatDate } from "@/common/js/util"
import { callPuExctQryToOICS } from "@/api/api"
import OFooter from "@/components/common/OFooter"
import OUploader from "@/components/common/OUploader"
import $ from "jquery"
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
Group, XInput, XTextarea, XButton, ViewBox, OFooter, OUploader, Flexbox, FlexboxItem, AlertModule, Loading
},
data() {
return {
list: [],//页面数据集合
data: {},//页面数据
pageData: {},//父页面传入参数
propUploaderTitle: '上传图片',//图片控件标题
propLableStyle: { width: '6.5em', textAlign: 'justify', marginRight: '2em' },//图片控件标题样式
propMaxCount: 50,//上传图片最大数量
uploadList: [],////初始化，选择的图片列表，待上传至服务器
propSuccessImgList: [],//初始化，已经上传成功的图片列表，接口返回数据
disabledButton: false,
reqUploadList: [],//需要执行批次上传的图片集合，现在每次上传2张
submintState: '',//"01-保存，02-提交"
pageIndex: 0,//查询图片当前页
pageSize: 2,//查询图片每页数量
loadingShow: false,
loadingText: '请求中',
imgMax: 0,//待上传的图片总数
imgCount: 0,//上传成功的图片数量
isDefault: false,//是否显示图片上传、加载进度
}
}, created() {

},
watch: {

},
methods: {

replaceValue(value) {
this.data.FeedbackComment = value.replace(/ /g, '').replace(/\u2006/g, '');
},

getFeedbackDetail() {
let params = {
params: {
RuleName: "GetFeedbackEventDetail",
Parm: { EventNo: this.pageData.eventNo }
}
};
let callBackSuccess = data => {
if (data.ReturnCd == '00') {
this.data = data.RspMsg.result.ListItems.ListItem[0]
}
else {
this.showModule("获取数据失败", data.RspMsg.msg);
}
if (this.pageData.feedbackStatus != '1') {
this.loadingShow = false
}
};
callPuExctQryToOICS(params, callBackSuccess);
},//查询图片
getImageItems() {
if (this.loadingShow == false) {
return
}
this.pageIndex++
let params = {
params: {
RuleName: "GetImageItems",
Parm: {
CaseNo: this.pageData.eventNo
, PageSize: this.pageSize
, PageIndex: this.pageIndex
}
}
};
let callBackSuccess = data => {
if (data.ReturnCd == '00') {
this.propSuccessImgList = this.propSuccessImgList.concat(data.RspMsg.result.ListItems.ListItem || []);
setTimeout(() => {
this.imgMax = data.RspMsg.total
this.imgCount = this.imgCount + data.RspMsg.result.ListItems.ListItem.length
if (!data.RspMsg.result.ListItems.ListItem || this.imgCount == data.RspMsg.total || data.RspMsg.result.ListItems.ListItem.length != this.pageSize) {
this.loadingShow = false
}
else {
this.getImageItems()

}
}, 10)
}
else {
this.loadingShow = false
this.showModule("获取图片失败", data.RspMsg.msg);
}
};
let callBackError = data => {
this.loadingShow = false
this.showToast('获取图片失败', '20.0em', 'warn')
};
callPuExctQryToOICS(params, callBackSuccess, callBackError);
},//弹出信息提示窗
showModule(title, content) {
AlertModule.show({
title: title,
content: content,
onShow() {

},
onHide() {

}
})
}, showToast(text, width, type) {
this.$vux.toast.show({
text: text,
type: type,//success, warn, cancel, text
width: width
})
},//获取上传的图片信息
getUploadList(uploadList) {
this.uploadList = uploadList


},//录入预警查证反馈意见任务处理-保存、提交
handleInputEvent(submintState) {

this.submintState = submintState
let errMsg = submintState == '01' ? '录入预警查证反馈意见保存失败' : '录入预警查证反馈意见提交失败'
if (this.data.FeedbackComment == '') { this.showToast('请输入查证反馈意见', '10.0em', 'cancel'); return }

this.loadingText = '请求中'
this.loadingShow = true
this.isDefault = false
this.disabledButton = true
const _this = this
let params = {
params: {
RuleName: "HandleFeedbackEvent",
Parm: {
TimePoint: formatDate(new Date(), 'yyyyMMdd')
, EventNo: this.pageData.eventNo
, FeedbackComment: this.data.FeedbackComment
, HandleState: submintState
}
}
};
let callBackSuccess = data => {

if (data.ReturnCd == '00' && data.RspMsg.result[0].DataFlag == '01') {









if (this.$refs.uploader.addImgCount > 0) {



for (let i = 0; i < this.uploadList.length; i++) {
let item = this.uploadList[i]
if (item.ImageId < 0) {
this.reqUploadList.push({ ImageId: item.ImageId, FileName: item.FileName, FileContent: item.src.slice(23) })
}
}
this.imgMax = this.reqUploadList.length
this.imgCount = 0

this.handleImageItems()
}
else {

this.loadingShow = false
this.$vux.toast.show({
text: this.submintState == '01' ? '录入预警查证反馈意见保存成功' : '录入预警查证反馈意见提交成功',
width: '15em',
onHide() {
_this.$router.go(-1)
}
})
}

if (this.$refs.uploader.deleteImgList.length > 0) { this.deleteHandleImageItems() }
}
else {
this.loadingShow = false
this.showModule(errMsg, data.RspMsg.msg);
this.disabledButton = false
}
};
let callBackError = data => {
this.loadingShow = false
this.showModule(errMsg, data.RspMsg.msg);
this.disabledButton = false
};
callPuExctQryToOICS(params, callBackSuccess, callBackError);

},//上传图片
handleImageItems() {


const _this = this
let imgList = this.reqUploadList.splice(0, 2)
this.loadingText = "上传图片中"
this.isDefault = true
this.imgCount = this.imgCount + imgList.length
let params = {
params: {
RuleName: "HandleImageItems",
Parm: {
CaseNo: this.pageData.eventNo,
HandleState: '02'
, ListItems: {
ListItem: imgList
}
}
}
};
let callBackSuccess = data => {

if (data.ReturnCd == '00' && data.RspMsg.result[0].DataFlag == '01') {
if (this.reqUploadList.length > 0) {
this.handleImageItems()
}
else {

this.loadingShow = false
this.$vux.toast.show({
text: this.submintState == '01' ? '录入预警查证反馈意见保存成功' : '录入预警查证反馈意见提交成功',
width: '15em',
onHide() {
_this.$router.go(-1)
}
})
}
}
else {
this.loadingShow = false
this.showModule('上传图片失败', data.RspMsg.msg);

}
};
let callBackError = data => {
this.loadingShow = false
this.showToast('上传图片失败', '20.0em', 'warn')

};
callPuExctQryToOICS(params, callBackSuccess, callBackError);
},//删除图片
deleteHandleImageItems() {
let params = {
params: {
RuleName: "HandleImageItems",
Parm: {
CaseNo: this.pageData.eventNo,
HandleState: '04'
, ListItems: {
ListItem: this.$refs.uploader.deleteImgList
}
}
}
};
let callBackSuccess = data => {

let item = null
if (data.ReturnCd == '00' && data.RspMsg.result[0].DataFlag == '01') {

}
else {
_this.showModule('删除图片失败', data.RspMsg.msg);
}
};
let callBackError = data => {
this.loadingShow = false
this.showToast('删除图片失败', '20.0em', 'warn')

};
callPuExctQryToOICS(params, callBackSuccess, callBackError);
},
getImgCount() {
return this.imgCount + '/' + this.imgMax
}
},
mounted() {
},
beforeRouteEnter(to, from, next) {
next(vm => {
vm.loadingText = '加载中'
vm.loadingShow = true
vm.isDefault = false
vm.list = []
vm.data = {}
vm.uploadList = []
vm.propSuccessImgList = []
vm.disabledButton = false
vm.pageIndex = 0
vm.$refs.uploader.addImgCount = 0
vm.reqUploadList = []
vm.submintState = ''
vm.pageData.eventNo = vm.$route.query.eventNo;
vm.pageData.feedbackStatus = vm.$route.query.feedbackStatus;
vm.$refs.uploader.deleteImgList = []
vm.imgMax = 0
vm.imgCount = 0
vm.getFeedbackDetail()

if (vm.pageData.feedbackStatus == '1') {
vm.loadingText = "图片加载中"
vm.isDefault = true
vm.getImageItems()
}
})
},//控制浏览大图不返回
beforeRouteLeave(to, from, next) {
let isClickImg = window.sessionStorage.getItem("isClickImg")
if (isClickImg == '0') {
this.loadingShow = false
next()
}
else {
this.$refs.uploader.$refs.previewer.close()
next(false)
}
}
}



